@use '../abstracts/index.scss' as a;

.footer {
  width: 100%;
  height: 6rem;
  background: a.$color-primary-dark;
  display: flex;
  align-items: center;
  .copyright {
    padding: 4rem;
    color: a.$color-secondary-light;
    font-size: 1.5rem;
    width: 100%;
    height: 100%;

    & a {
      text-decoration: none;
      color: inherit;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}
