@use '../abstracts/index.scss' as a;

.nav {
  position: absolute;
  width: 100%;
  height: 6rem;
  display: flex;
  justify-content: flex-start;

  // nav menu button
  &__toggle {
    padding: 2em 1em;
    border: 0;
    border-radius: 0.25em 0 0 0.25em;
    background-color: inherit;
    z-index: 9999;

    &:hover {
      cursor: pointer;
    }

    .hamburger {
      display: block;
      position: relative;
      transition: all 0.4s ease;

      &,
      &::before,
      &::after {
        width: 2em;
        height: 3px;
        background: a.$color-secondary-light;
      }
      &::before,
      &::after {
        content: '';
        position: absolute;
        right: 0;
      }
      &::before {
        bottom: 9px;
      }
      &::after {
        top: 9px;
      }
    }
  }
  // nav menu
  &__menu {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    visibility: hidden;
    overflow: hidden;
    z-index: 999;
  }
  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 40rem;
    height: 40rem;
    background: a.$color-secondary-light;
    border-radius: 50%;
    transform: scale(0);
    transition: all 0.4s ease;
  }
  @media (max-width: 40em) {
    &__list {
      width: 30rem;
      height: 30rem;
    }
  }
  &__item {
    margin-bottom: 1em;
    font-weight: 300;
    font-size: 3rem;
    opacity: 0;
    transition: opacity 0.4s ease-in;
    &:hover {
      text-decoration: underline;
    }
  }

  &__link {
    text-decoration: none;
    color: a.$color-primary-dark;
  }

  // on hamburger click
  // menu

  &-active &__menu {
    visibility: visible;
    backdrop-filter: brightness(20%);
    transition-duration: 0.75s;
  }
  &-active &__list {
    transform: scale(1);
    transition-duration: 0.75s;
  }
  &-active &__item {
    opacity: 1;
    transition-duration: 1s;
  }

  // hamburger
  &-active .hamburger {
    transform: rotate(135deg);
  }

  &-active .hamburger::before,
  &-active .hamburger::after {
    top: 0;
    transform: rotate(90deg);
  }
}
