@use '../abstracts/index.scss' as a;

*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

html,
body {
  padding: 0;
  margin: 0;
  background-color: a.$color-primary-dark;
}

body {
  height: 100vh;
  width: 100vw;
  overflow: auto;
  overflow-x: hidden;
  font-family: 'Inter', sans-serif;
  //position: relative;
}
