@use '../abstracts/index.scss' as a;

.section {
  min-height: 100vh;
  min-width: 100vw;
  margin: 0;
  scroll-margin-top: 6rem;

  &__hero {
    background: radial-gradient(a.$color-primary-dark 15%, transparent 16%) 0 0,
      radial-gradient(a.$color-primary-dark 15%, transparent 16%) 8px 8px,
      radial-gradient(rgba(255, 255, 255, 0.1) 15%, transparent 20%) 0 1px,
      radial-gradient(rgba(255, 255, 255, 0.1) 15%, transparent 20%) 8px 9px;
    background-color: a.$color-primary;
    background-size: 16px 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-shadow: 0 3px black;

    .socials {
      min-width: 25%;
      min-height: 10%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 1rem;
    }
    .link {
      font-size: 4rem;
      color: a.$color-tertiary;
      margin-right: 3rem;

      &__linkedin {
        margin-right: 0;
      }

      &__contact:hover {
        color: a.$color-gmail;
      }
      &__github:hover {
        color: a.$color-black;
      }
      &__linkedin:hover {
        color: a.$color-linkedin;
      }
    }
  }

  &__skills {
    background-image: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0.85),
        rgba(0, 0, 0, 0.85)
      ),
      url(../img/coding.jpg);
    background-size: cover;
    background-attachment: scroll;
    background-repeat: no-repeat;

    .skills {
      width: 100%;
      height: 90vh;
      display: flex;
      align-items: center;

      &__list {
        list-style: none;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-basis: 100%;
        flex-wrap: wrap;
      }
      &__item {
        font-size: 13rem;
        display: flex;
        flex-direction: column;
        flex-basis: 100%;
        flex: 1 1;
      }
    }
  }

  &__projects {
    background: a.$color-primary;

    .project {
      &__grid {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(28rem, 1fr));
        gap: 1rem;
        margin: 0 4rem;
        padding-bottom: 2rem;
      }

      &__card {
        display: grid;
        grid-template-columns: subgrid;
        grid-template-rows: min-content;
        gap: 1em;

        background: a.$color-secondary-dark;
        padding: 3rem;
        color: a.$color-white;

        &-title {
          font-size: 3rem;
          margin: 0;

          & a {
            text-decoration: none;
            color: a.$color-white;

            &:hover {
              text-decoration: underline;
            }
          }
        }
        &-subtitle {
          font-size: 1.5rem;
        }
        &-desc {
          font-size: 1.5rem;
          font-weight: 500;
          color: a.$color-tertiary;
          margin: 0;
        }

        &-link {
          display: flex;
          align-items: flex-end;

          & a {
            width: 100%;
            height: 3rem;
            text-decoration: none;
            color: white;
            font-size: 2rem;
            padding: 2rem;
            display: flex;
            align-items: center;
            margin-top: 2rem;
            white-space: nowrap;
          }
        }

        .left-link {
          color: a.$color-html;
          margin-right: 2rem;
          justify-content: center;
          border: 1px solid a.$color-html;
          background-image: linear-gradient(
            45deg,
            a.$color-html 50%,
            a.$color-secondary-dark 50%
          );
          background-size: 250% 100%;
          background-position: right bottom;
          transition: all 0.5s ease-in-out;

          &:hover {
            &::before {
              content: '< ';
              font-size: 1.5rem;
            }
            background-position: left top;
            color: a.$color-white;

            &::after {
              content: ' />';
              font-size: 1.5rem;
            }
          }
        }

        .right-link {
          color: a.$color-bootstrap;
          justify-content: center;
          border: 1px solid a.$color-bootstrap;
          background-image: linear-gradient(
            315deg,
            a.$color-bootstrap 50%,
            a.$color-secondary-dark 50%
          );
          background-size: 250% 100%;
          background-position: left top;
          transition: all 0.5s ease-in-out;

          &:hover {
            &::before {
              content: '< ';
              font-size: 1.5rem;
            }
            background-position: right bottom;
            color: a.$color-white;

            &::after {
              content: ' />';
              font-size: 1.5rem;
            }
          }
        }
      }
    }
  }
}
